import React, { useEffect, useState } from "react";
import { Container, Row, Col, Button } from "reactstrap";
import { getTourPackage } from "../actions/packageActions";
import "../App.css";

import { useDispatch, useSelector } from "react-redux";
import axios from "axios";

const Tour = () => {
  const dispatch = useDispatch();
  const user = useSelector((state) => state.user);
  const tour = useSelector((state) => state.package);
  const [packageId, setPackageId] = useState("");

  useEffect(() => {
    setPackageId(window.location.search.substring(1));
    axios
      .get(`/packages/${packageId}`)
      .then((res) => {
        dispatch(getTourPackage(res.data[0]));
        console.log("Tour:", tour[0]);
      })
      .catch((err) => console.error(err));
  }, []);

  const addItemToWishlist = (packageId) => {
    axios
      .patch(`wishlist/add/${packageId}`, { user_id: user.id })
      .catch((err) => console.error(err));
  };

  return (
    <div className="subComponent">
      <Container>
        <section className="tour-cover item-center">
          <img src={`image/${tour[0].filename}`} alt="" />
          <h1>{tour[0].title}</h1>
          <h4>{tour[0].details}</h4>
        </section>
        <section className="tour-info">
          <Row>
            <Col sm="8">
              <div className="tour-desc">
                <p>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit. In
                  imperdiet, sem id vehicula lacinia, mi purus lacinia mauris,
                  vitae mollis nisl elit in lorem. Suspendisse potenti. Cras
                  elementum ullamcorper tortor, rutrum convallis nunc tempor
                  tristique. Donec ut ipsum vel mauris hendrerit efficitur.
                  Nullam eget massa interdum, euismod nunc ac, maximus risus.
                  Aliquam nec rhoncus tortor. Suspendisse nulla diam, hendrerit
                  a metus vitae, rutrum hendrerit nisl. Nulla vel venenatis
                  massa. Mauris lacinia porttitor ex, a egestas nisi fringilla
                  vitae. Nam fringilla leo ante, id interdum sapien facilisis
                  vel. Morbi ut suscipit nulla. Sed vitae tempus elit, at
                  laoreet urna.
                </p>
                <p>
                  Morbi facilisis, odio vitae egestas pretium, mauris tortor
                  vulputate dolor, non interdum nunc tellus at dolor. Donec
                  condimentum et augue vitae volutpat. Fusce vitae laoreet sem.
                  Integer a justo sit amet nibh vehicula blandit. Suspendisse
                  faucibus venenatis egestas. In vulputate sapien sit amet
                  ligula vulputate, in consequat ex molestie. Curabitur
                  hendrerit pulvinar vehicula. Phasellus quis posuere tortor.
                  Cras pellentesque vehicula dui nec fermentum. Sed venenatis
                  nunc justo. Quisque metus est, volutpat quis scelerisque in,
                  fermentum sed lacus. Sed sed pretium massa. Aenean imperdiet
                  molestie turpis at egestas.
                </p>
              </div>
            </Col>
            <Col
              sm="4"
              style={{
                display: "flex",
                justifyContent: "space-between",
                flexDirection: "column",
              }}>
              <div className="tour-gallery">
                {tour[0].showcaseImages.map((image) => (
                  <img src={`/image/${image}`} alt="Showcase" />
                ))}
              </div>
              <Button
                outline
                color="primary"
                style={{ width: "100%", marginBottom: "20px" }}
                onClick={addItemToWishlist(packageId)}>
                Add To Wishlist
              </Button>
            </Col>
          </Row>
        </section>
      </Container>
      <section className="reviews">
        <Container>
          <section className="tour-msg text-center">
            <h1>Reviews and Suggestions</h1>
            <p>We're glad to hear something from you.</p>
            <form action="">
              <Row>
                <Col sm="6">
                  <input
                    type="text"
                    name="Name"
                    id="reviewer-name"
                    placeholder="Your Name"
                    required
                  />
                  <br />
                  <input
                    type="email"
                    name="Email"
                    id="reviewer-email"
                    placeholder="Your email"
                    required
                  />
                </Col>
                <Col>
                  <textarea
                    name="Message"
                    id="reviewer-message"
                    rows="4"
                    placeholder="Your Message"
                  />
                </Col>
              </Row>
              <Button outline color="secondary" className="float-right">
                Submit
              </Button>
            </form>
          </section>
        </Container>
      </section>
    </div>
  );
};

export default Tour;
