import React, { useState } from "react";
import { Form, FormGroup, Input, Label, FormText, Button } from "reactstrap";
import axios from "axios";

export default function SubmitPackage() {
  const [title, setTitle] = useState("");
  const [details, setDetails] = useState("");
  const [categories, setCategories] = useState("");
  const [description, setDescription] = useState("");

  const checkCategories = (categories) => {
    return categories
      .toLowerCase()
      .split(",")
      .map((category) => category.replace(/\s/g, ""));
  };

  const submitForm = async () => {
    try {
      await axios
        .get("/files/filenames")
        .then((res) => {
          const filenames = res.data;
          const showcase = filenames.slice(
            Math.max(filenames.length - filenames.length + 1, 1)
          );
          console.log(res);
          const categoryArray = checkCategories(categories);
          axios
            .post("/packages/add_package", {
              title: title,
              details: details,
              category: categoryArray,
              description: description,
              filename: filenames[0],
              showcaseImages: showcase,
            })
            .catch((err) => console.error(err));

          setTitle("");
          setCategories("");
          setDescription("");
          setDetails("");
        })
        .catch((err) => console.error(err));
    } catch (err) {
      console.error(err);
    }
  };
  return (
    <Form
      action="/upload"
      method="POST"
      encType="multipart/form-data"
      onSubmit={submitForm}>
      <FormGroup>
        <Label htmlFor="title">Title</Label>
        <Input
          type="text"
          name="title"
          onChange={(e) => setTitle(e.target.value)}
        />
      </FormGroup>
      <FormGroup>
        <Label htmlFor="details">Details</Label>
        <Input
          type="text"
          name="details"
          onChange={(e) => setDetails(e.target.value)}
        />
      </FormGroup>
      <FormGroup>
        <Label htmlFor="categories">Categories</Label>
        <Input
          type="text"
          name="categories"
          onChange={(e) => setCategories(e.target.value)}
        />
        <FormText htmlFor="categories">
          Be sure to put a comma after each category (',')
        </FormText>
      </FormGroup>
      <FormGroup>
        <Label htmlFor="package_image">Image</Label>
        <Input type="file" name="package_image" />
        <FormText htmlFor="package_image">
          Select Package Thumbnail Image
        </FormText>
      </FormGroup>
      <FormGroup>
        <Label htmlFor="package_image">Showcase Images</Label>
        <Input type="file" name="showcase" multiple />
        <FormText htmlFor="showcase">Select Package Thumbnail Image</FormText>
      </FormGroup>
      <FormGroup>
        <Label htmlFor="title">Description</Label>
        <Input
          type="textarea"
          name="description"
          onChange={(e) => setDescription(e.target.value)}
        />
      </FormGroup>
      <Button>Add Package</Button>
    </Form>
  );
}
