import React, { useState, useEffect } from "react";
import "../App.css";
import Hero from "../components/Hero";
import About from "../components/About";
import Package from "../components/Package";
import Services from "../components/Services";
import Contact from "../components/Contact";
import Footer from "../components/Footer";
import { useSelector } from "react-redux";
import axios from "axios";

const HomePage = () => {
  const user = useSelector((state) => state.user);
  /* const wishlist = useSelector((state) => state.wishlist); */
  const [wishlistCreated, setWishlistCreated] = useState(false);

  useEffect(() => {
    if (!wishlistCreated) {
      axios.post("/wishlist/create", { user_id: user.id });
      setWishlistCreated(!wishlistCreated);
    }
    axios
      .get(`/wishlist/${user.id}`)
      .catch((err) =>
        console.error("Couldn't get data! There was an error: ", err)
      );
  }, [wishlistCreated, user.id]);
  return (
    <div id="home">
      <Hero />
      <About />
      <Package />
      <Services />
      <Contact />
      <Footer />
    </div>
  );
};

export default HomePage;
